<template>
	<view class="user-nickname">
		<view class="box"><input type="text" :value="name" placeholder="姓名" placeholder-style="input-placeholder" @input="input1" /></view>
		<view class="box"><input type="text" :value="identityCard" placeholder="身份证号" placeholder-style="input-placeholder" @input="input2" /></view>
		
		<view class="btn" @click="submit">提交认证</view>
	</view>
</template>

<script>
	import {
		API
	} from '@/util/api.js';
	export default {
		data() {
			return {
				name: '',
				identityCard:''
			};
		},
		methods: {
			inpu1(e) {
				this.name = e.target.value;
			},
			input2(e) {
				this.identityCard = e.target.value;
			},
			submit() {
			
					var that = this;
					console.log(that.name)
					this.request(API.nickName, 'get', {
						name: that.name
					}).then(res => {
						if (res.code === 10000) {
							uni.showToast({
								title: '提交成功',
								duration: 2000,
								success: function() {
									setTimeout(() => {
										uni.navigateBack()
									}, 2000)
			
								}
							});
						}
					})
			
				}
			
		}
	}	
</script>

<style lang="scss">
	@import '../../assets/css/user/nickname.scss';
</style>
